import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';

import * as CollapsibleTable_Main from './CollapsibleTable_Main'

import * as DataGrid from './DataGrid_Eq'


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 1000,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};


export function Eq(props) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const test = (e) => {
    handleClose()
    props.EqSlctOn(props.SlctMain, e)

  }


  return (
    <div>
      <Button onClick={handleOpen}>
        <img
          src={`${process.env.PUBLIC_URL}/${props.EqNam}i.png`}
          style={{ width: 35 }}
          alt={''}
        />
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Box sx={{ ...style, width: 1000 }}>
          <DataGrid.Eq
            Eq={props.Eq}
            EqData={props.EqData}
            SlctMainCls={props.SlctMainCls}
            SlctMain={props.SlctMain}
            EqSlctOn={props.EqSlctOn}
            handleClose={handleClose}

          />

        </Box>
      </Modal>
    </div>
  );
}