import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import * as DataGrid from './DataGrid_Act'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};




export function Act(props) {
  // console.log(props.Act)
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const ActIcon = Object.keys(props.Act).map((t) => {
    return (
      <img
        src={`${process.env.PUBLIC_URL}/${props.Act[t].Nam}i.png`}
        style={{ width: 35 }}
        alt={''} />
    );
  });

  return (
    <div>
      <Button onClick={handleOpen}>{ActIcon}</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Box sx={{ ...style, width: 1000 }}>
          <DataGrid.Act
            SlctMain={props.SlctMain}

            Act={props.Act}
            ActList={props.ActList}
            ActSlctOn={props.ActSlctOn}
            handleClose={handleClose}
          />

        </Box>
      </Modal>
    </div>
  );
}