import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import * as DataGrid from './DataGrid_ActSlct'

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 800,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export function ActSlct(props) {//行動パターンのアイコン押下で行動選択画面
  // console.log(props.ActList)
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose2 = () => setOpen(false);

  return (
    <div>
      <Button onClick={handleOpen} >
        <img src={`${process.env.PUBLIC_URL}/${props.ActNam}i.png`} style={{ width: 35 }} alt={''} />
      </Button>
      <Modal
        open={open}
        onClose={handleClose2}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <DataGrid.ActSlct
            SlctMain={props.SlctMain}
            t={props.t}
            ActList={props.ActList}
            ActSlctOn={props.ActSlctOn}
            
            handleClose2={handleClose2}
            handleClose={props.handleClose}
            />
        </Box>
      </Modal>
    </div>
  );
}