import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Button from "@material-ui/core/Button";


export function Eq(props) {

  const test = (e) => {
    props.handleClose()
    props.EqSlctOn(props.SlctMain, props.Eq, e)
  }

  const columns = [
    {
      field: 'id',
      headerName: '',
      width: 40,
      headerAlign: 'center',
      align: 'center',
      renderCell: (params) => (
        <Button onClick={() => test(params.row.name)} >
          <img src={`${process.env.PUBLIC_URL}/${params.row.name}i.png`} style={{ width: 35 }} alt={''} />
        </Button>
      )
    }, {
      field: 'name',
      headerName: '名称',
      width: 200,
    }, {
      field: 'HP',
      headerName: 'HP',
      type: 'number',
      width: 40,
    }, {
      field: 'A',
      headerName: 'アタック',
      type: 'number',
      width: 40,
    }, {
      field: 'D',
      headerName: 'ディフェンス',
      type: 'number',
      width: 40,
    }, {
      field: 'M',
      headerName: 'マジック',
      type: 'number',
      width: 40,
    }, {
      field: 'R',
      headerName: 'レジスト',
      type: 'number',
      width: 40,
    }, {
      field: 'T',
      headerName: 'テクニック',
      type: 'number',
      width: 40,
    },

  ];


  const rows = []
  Object.keys(props.EqData).forEach(EqNam => {
    if (props.EqData[EqNam][props.SlctMainCls]) {
      rows.push({
        id: EqNam,
        name: EqNam,
        HP: props.EqData[EqNam].H,
        A: props.EqData[EqNam].A,
        D: props.EqData[EqNam].D,
        M: props.EqData[EqNam].M,
        R: props.EqData[EqNam].R,
        T: props.EqData[EqNam].T,
      })
    }
  })

  return (
    <Box sx={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        density='compact'//行の幅
        pageSize={5}
        rowsPerPageOptions={[5]}
        disableRowSelectionOnClick
      />
    </Box>
  );
}