import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Button from "@material-ui/core/Button";








export function Sprt(props) {
  // console.log(props.Sprt)
  // console.log(props.SprtData)

  const test = (e) => {
    props.handleClose()
    props.SprtSlctOn(props.SlctMain, e)
  }

  const columns = [
    {
      field: 'id',
      headerName: 'ID',
      width: 80,
      renderCell: (params) => (
        <Button onClick={() => test(params.row.name)} >
          <img src={`${process.env.PUBLIC_URL}/${params.row.name}i.png`} style={{ width: 80 }} alt={''} />
        </Button>
      )
    },{
      field: 'name',
      headerName: 'サポートキャラ',
      width: 150,
      //editable: true,
    },
  ];
  





  const rows = []

  Object.keys(props.SprtData).forEach(Sprt => {

    rows.push({
      id: Sprt,
      name: Sprt,
    })
  })









  return (
    <Box sx={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        pageSize={5}
        rowsPerPageOptions={[5]}
        // disableRowSelectionOnClick
      />
    </Box>
  );
}