import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';

import * as CollapsibleTable_Main from './CollapsibleTable_Main'
import * as CollapsibleTable_Sprt from './CollapsibleTable_Sprt'
import * as DataGrid_Sprt from './DataGrid_Sprt'


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};



export function Sprt(props) {

  // console.log(props.Sprt)

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button onClick={handleOpen}>
        <img src={`${process.env.PUBLIC_URL}/${props.Sprt.Nam}i.png`} style={{ width: 80 }} alt={''} />
      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="parent-modal-title"
        aria-describedby="parent-modal-description"
      >
        <Box sx={{ ...style, width: 600 }}>
          <DataGrid_Sprt.Sprt
          SlctMain={props.SlctMain}
          Sprt={props.Sprt}
          SprtData={props.SprtData}
          SprtSlctOn={props.SprtSlctOn}
          handleClose={handleClose}
          />
          
        </Box>
      </Modal>
    </div>
  );
}

